.about_container{
    display :grid;
    grid-template-columns: 35% 50%;
   
        
  

}     
h3 a{
    color: bisque;
    list-style:none;
}                              
.intro{
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: bisque;
    margin-bottom: 1.75rem;
    margin-top: 1.75rem;
}

.about_me{
    width: 300px;
    
    border-radius: 2rem;
     height: 300px;
     background-color:hsla(0, 1%, 28%, 0.822);
     border-radius: 2rem;
}
.aimg{
    border-radius: 2rem;
    width: 300px;
    height: 300px;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);

}
.about_me-img :hover{
    transform: rotate(0);
}
.about_cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 1.5rem; 
        

}
.about_card{
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    background-color:hsla(0, 1%, 28%, 0.822);
    font-size: 20px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: bisque;
   


}
button a{
    color: bisque;
}
button a:hover{
    color: black;
   
}
.about_card:hover {
    background: transparent;
    border-color: hsla(0, 1%, 28%, 0.822);

}
.about_content{
    text-align: start;
}

.about_card small {
    font-size: 0.9rem;
    font-weight: lighter;

}
.about_content p{
   margin-top: 2rem;
    margin-bottom: 1rem;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    
    
}
p{
    color: whitesmoke;
}
.intro h1{
    font-size: 1.5rem;
}
.intro h2{
   color:hsla(0, 1%, 28%, 0.822);
   font-size: 2.5rem;
}

@media screen and (max-width: 1024px){
    .about_container{
        grid-template-columns: 1fr;

    }
    .about_me{
       
        margin: 2rem auto 4rem;

    }
    .about_content p{
        margin:1rem 0 1.5rem;
         
    }
    
}


@media screen and (max-width: 600px){
 
    .about_me{
       
        margin: 0 auto 3rem;

    }
     .about_cards{
               grid-template-columns: 1fr ;
               gap: 1rem;
              

     }
     .about_content{
        text-align: center;

     }
     .about_content p{
        margin: 1.5rem 0;    
        text-align: start;         
        
     }
}
