.intro_con{
    margin-top: 3rem;
    margin-bottom: 1.75rem;
}
.intro_con h1{
    color:hsla(0, 1%, 28%, 0.822);
    font-size: 2.5rem;
    margin-bottom: 0;
    
}
.intro_con h3{
    font-size: 1.5rem;
    color: bisque;
    margin-bottom: 0;
}
.con_container{
    display: grid;
    grid-template-columns: 1fr 1fr;


}
.options{
    display: grid;
    grid-template-columns: 45% 45%;
    gap: 1.5rem; 
    margin-bottom: 1.5rem;

        

}
.options :hover{
    background: transparent;
    border-color: hsla(0, 1%, 28%, 0.822);
}
.contact_card{
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    background-color:hsla(0, 1%, 28%, 0.822);
    font-size: 20px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: bisque;
   


}
.btn{
    
    justify-content:left;
  display: flex;
}
input,textarea{
    background-color: transparent;
    border-color: hsla(0, 1%, 28%, 0.822);

}
.form-control{
    background-color: transparent;
    
    border-color: hsla(0, 1%, 28%, 0.822);
}
@media screen and (max-width:1024px){
    .con_container{
        grid-template-columns: 1fr;

    }
    .options{
        grid-template-columns: repeat(3,1fr);
    }
    a{
        font-size: 1.1rem;
    }

}
@media screen and (max-width:600px){
    
    .options{
        grid-template-columns: 1fr ;
    }
   
    


}