.profile_container{
    text-align: center;
    gap: 5%;
    height: 100vh;
    position: relative;

}
.po{
    display: flex;
    justify-content: center;
  
}
.profile-parent{
    display: flex;
    align-items: center;
    color: bisque;
    justify-content: center;
    
}
.profile-details{
    width: 350px;
}
.profile-detail-name{
    font-size: 20px;
    font-family:cursive;
}
.profile-detail-role{
    font-size: 30px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    flex-direction: column;
    margin: 14px 0 24px 0;
}
.profile-option{
    margin-bottom: 10px;
}
.profile-option a{
    list-style: none;
    text-decoration: none;
}
.profile-option.btn{
    padding: auto;
}
.profile-option .btn:hover{
    background-color: rgb(240, 243, 245);
    color: black;

}
.profile-picture{
    display: flex;
    

   
   
    margin: 30px 0 100px;
    margin-bottom: 30px;
}
.pic{
   
    border-radius: 12rem 12rem 0 0;
    width: 25rem;
    height: 25rem;
   
}
@media screen and (max-width: 1024px){
.profile_container{
    height: 100vh;
}
