.foot{
    background-color:hsla(0, 1%, 28%, 0.822);
    padding: 3rem 0;
    text-align: center;
 
    margin-top: 7rem;
    font-size: 0.9rem;
    color: bisque;
}
ul{
    display: flex;
    flex-wrap: wrap;
    gap: 3rem;
    justify-content: center;
    text-decoration: none;
    list-style: none;
}
.footer_soicial{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

li a{
    color: bisque;
}
.footer_soicial a{
    
    padding: 0.6rem;
    border-radius: 0.4rem;
    display: flex;
    border: 1px solid transparent;
    justify-content: center;
    margin: 0;
    font-size: 1.5rem;
 

}
@media screen and (max-width:600px){
    ul{
        flex-direction: column;
        gap: 1.5rem;
        
    }
    .footer_soicial{
        margin-bottom: 1.6rem;
    }
}
